import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import '../assets/styles/components/_netslider.scss';
import Slide from './Slide';


const Netslider = ({elements, setElements, openModal, title}) => {
    
    const handleOpen = (id) => {
        setElements(elements.find(x => x.id === id));
        openModal(true);

    };

    return (
        <div className="netslider-wrapper">
            <h2>{title}</h2>
            <Swiper
                modules={[Navigation]}
                spaceBetween={20}
                slidesPerView={4}
                navigation
                className='section'
                breakpoints={{
                    // when window width is >= 640px
                    360: {
                        width: 360,
                        slidesPerView: 1,
                        spaceBetween:0
                    },
                    375: {
                        width: 375,
                        slidesPerView: 1,
                        spaceBetween:0
                    },
                    390: {
                        width: 390,
                        slidesPerView: 1,
                        spaceBetween:0
                    },

                    414: {
                        width: 414,
                        slidesPerView: 1,
                        spaceBetween:0
                    },
                    // when window width is >= 768px
                    768: {
                        width: 768,
                        slidesPerView: 2,
                        spaceBetween:10
                    },
                    820: {
                        width: 820,
                        slidesPerView: 2,
                        spaceBetween:10
                    },
                    895: {
                        width: 895,
                        slidesPerView: 3,
                        spaceBetween:20
                    },
                }}
            >
                {elements.map(element=> (
                    <SwiperSlide 
                        className='item' 
                        key={element.id} 
                        onClick={()=> handleOpen(element.id)}
                    >
                        <Slide element={element} />
                    </SwiperSlide>
                ))};
                
            </Swiper>
        </div>
    );
};

export default Netslider;